<script>
import { Avatar, Bicycle } from '@element-plus/icons-vue'
import { routes, routePathMap } from "@/admin/router";

export default {
  name: "NavBar",
  components: { Avatar, Bicycle },
  props: {
    routes: {type: Array},
    collapse: { type: Boolean, default: false }
  },
  methods: {
    hideInNav(each){
      if(each.hasOwnProperty("settings")){
        if(each.settings.hasOwnProperty("hideInNav")) return each.settings.hideInNav;
        else return false;
      }
      else return false;
    },
    setDefaultVal(){
        let path = this.$route.fullPath;
        let name = "";
        let arr = Object.keys(routePathMap);
        arr.forEach(each => {
          if(path.includes(each)){
            name = routePathMap[each];
          }
        });
        return name;
    },
    handleOpen(row, href = true) {
      let arr = routes.slice();
      let obj = this.parseArray(arr, row);
      this.$store.commit("setCurrentRoute", obj);
      if(href) {
        window.location.href = `#${obj.path}`;
      }
    },
    parseArray(row, val) {
      let routeObj = {};
      row.forEach(each => {
        if (each.name === val) {
          routeObj = {...each};
        }
        if (each.children) {
          each.children.forEach(each1 => {
            if (each1.name === val) {
              routeObj = {...each1};
              routeObj.path = `${each.path}/${each1.path}`;
            }
          });
        }
      });
      return routeObj
    }
  }
}
</script>

