<template>
  <div style="display: flex;height: 100%;width: 100%;">
    <div>
      <div style="width: 250px; height: 60px;display: flex;align-items: center;padding-left: 20px; background: #1e1e1e;">
        <span>
<!--          <img :src="require('../../assets/logo.png')" width="20" height="20" />-->
          <span style="font-size: 24px;margin-left: 10px;color: white; font-weight: 600;">LEGGO</span>
        </span>
      </div>
      <nav-bar :collapse="isCollapsed" :routes="getRoutes"  style="height: calc(100% - 60px);"></nav-bar>
    </div>
    <div style="width: calc(100% - 250px); background: #f8f8f8;">
      <div style="height: 60px; background: #1e1e1e; width: calc(100% - 11px); border-left: 1px solid rgba(232,232,232,0.66); display: flex; justify-content: flex-end;align-items: center; padding-right: 10px;">
        <img :src="require('../../assets/504-5040528_empty-profile-picture-png-transparent-png.png')" style="width: 40px; height: 40px; border-radius: 50%;" />
      </div>
      <div style="height: 40px;display: flex;padding-right: 20px; padding-left: 20px;align-items: center;font-size: 18px;font-weight: 600;">
        <div>{{ setDefaultVal() }}</div>
      </div>
      <router-view/>
    </div>
  </div>
</template>
<style>
#app {
  width: 100%;
  height: 100%;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

nav {
  padding: 30px;
}

nav a {
  font-weight: bold;
  color: #2c3e50;
}

nav a.router-link-exact-active {
  color: #42b983;
}
</style>
<script>
import { routes, routePathMap } from "../router/index";
import NavBar from "../components/NavBar";
export default {
  components: {NavBar},
  methods:{
    setDefaultVal(){
      let path = this.$route.fullPath;
      let name = "";
      let arr = Object.keys(this.routeMap);
      arr.forEach(each => {
        if(path.includes(each)){
          name = this.routeMap[each];
        }
      });
      return name;
    },
  },
  computed: {
    routeMap(){
      return routePathMap;
    },
    isCollapsed(){
      return this.$store.getters.isNavBarCollapsed;
    },
    getRoutes(){
      return routes;
    }
  }
}
</script>