import { createStore } from 'vuex'

export default createStore({
  state: {
    pageSettings: {
      collapse: false
    },
    currentRoute: {}
  },
  getters: {
    isNavBarCollapsed(state){
      return state.pageSettings.collapse;
    },
    getCurrentRoute(state){
      return state.currentRoute;
    }
  },
  mutations: {
    changeCollapse(state, newVal){
      state.pageSettings.collapse = newVal;
    },
    setCurrentRoute(state, newVal){
      state.currentRoute = newVal;
    }
  },
  actions: {

  },
  modules: {

  }
})
