import { createRouter, createWebHashHistory } from 'vue-router';

export const routePathMap = {
  "/": "root",
  "/dashboard": "Dashboard",
  "/drivers/view": "View Drivers",
  "/drivers/addDriver": "Add Driver",
  "/drivers/editDriver": "Edit Driver",
  "/rides": "Rides"
}

export const routes = [
  {
    path: '/',
    name: 'root',
    redirect: "dashboard",
    rootObj: true
  },
  {
    path: '/:pathMatch(.*)',
    name: 'wildcard',
    redirect: "/",
    rootObj: true
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard.vue'),
  },
  {
    path: '/drivers',
    name: 'Drivers',
    redirect: "/drivers/view",
    children: [
        {
          path: 'view',
          name: 'View Drivers',
          component: () => import(/* webpackChunkName: "about" */ '../views/ViewDrivers.vue'),
        },
      {
        path: 'addDriver',
        name: 'Add Driver',
        component: () => import(/* webpackChunkName: "about" */ '../views/AddDrivers.vue') ,
      },
      {
        path: 'editDriver/',
        name: 'Edit Driver1',
        settings: { hideInNav: true },
        redirect: "/drivers/view"
      },
      {
        path: 'editDriver/:driverId',
        name: 'Edit Driver',
        settings: { hideInNav: true },
        component: () => import(/* webpackChunkName: "about" */ '../views/AddDrivers.vue'),
      }
    ]
  },
  {
    path: '/rides',
    name: 'Rides',
    // redirect: "/rides/view",
    component: () => import(/* webpackChunkName: "about" */ '../views/ViewRides.vue')
    // children: [
    //   {
    //     path: 'view',
    //     name: 'View',
    //     component: () => import(/* webpackChunkName: "about" */ '../views/ViewRides.vue'),
    //   }
    // ]
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
